* {
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}





svg{
    padding-top:20px;
}
.label {
    font-size: 16px;
}
.tick-bottom:nth-child(n) text {
    visibility: hidden;
}
.tick-bottom:nth-child(4n+1) text {
    visibility: visible;
}

.tick-left:last-of-type text {
    visibility: hidden;
}
.Data {
    width: 380px;
    height: 890px;
    padding:10px;
    border-radius: 10px;
    background-color: ghostwhite;
    box-shadow:5px 10px 20px gray inset;
    position: absolute;
    left:0;
    top:calc(80px + 2vmin);
}
.Header {
    text-align: center;
    padding-top: 10px;
    font-size: 20px;
}
h4 {
    margin:0;
}
hr{
    width: 300px;
    height: 2px;
}
.Values{
    display:flex;
    flex-flow: row wrap;
    justify-content: space-around;
}
.Current {
    font-weight: 600;
    padding:5px;
}
p {
    font-size: 18px;
    font-weight: 800;
    text-align: center;
    margin: 20px 0 20px 0;
    font-style: italic;
    color: #888888;
}
.Historical {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.Box {
    width:280px;
    border: 1px solid;
    padding: 10px;
    margin:10px;
    box-shadow: 5px 8px 8px 8px #888888;
    display: flex;
    justify-content: space-between;
}
.button{
    background-color: #31A0D7;
    justify-content: flex-end;
    border-radius: 5px;
    padding: 6px;
    font-size: 12px;
    border: 1px solid black;
}

.legend{
     position: absolute;
     top:calc(80px + 2vmin);
     right: 10px;
     background-color: #ffffff;
     box-shadow: 8px 8px 30px #888888 inset;
     font-size: calc(3px + 2vmin);
     font-weight: 600;
     padding-bottom: calc(3px + 2vmin);
 }
header{
    text-shadow: 1px 1px #662804;
    text-align: center;
    font-weight: 800;
    margin: calc(3px + 2vmin);
}
.row {
    list-style: none;
}

.list span {
    display: inline-block;
    margin: 0 calc(3px + 2vmin);
    width:calc(3px + 2vmin);
    height: calc(3px + 2vmin);
    border-radius: 50%;
}
@media all and (min-width: 800px) {
    .legend {
        font-size: 20px;
        padding-bottom: 20px;
    }
    header{
        margin: 20px;
    }
    .list span {
        margin: 0 20px;
        width:20px;
        height: 20px;
        border-radius: 50%;
    }
}
.error{
    padding: 40px;
    border-radius: 10px;
    box-shadow: 8px 8px 30px #888888 inset;
    position: absolute;
    width:200px;
    height: 200px;
    margin:auto;
    top:0; left: 0; bottom: 0; right:0;
    border: 1px solid #000000;
    background-color: #ffffff;
    font-size: 18px;
    font-weight: 600;
    text-align: center;
}
.error p {
    font-size: 20px;
    margin:0;
    text-shadow: 1px 1px #662804;
    color: #000000;
    font-style: normal;
}
.Map {
    display: flex;
    height: 910px;
    border: 4px solid white;
}
.wrapper {
    position:relative;
}
#loader {
    z-index: 1000;
    border: 12px solid #394585;
    border-radius: 50%;
    height: 100px;
    left: 50%;
    margin: -50px 0 0 -50px;
    opacity: 0;
    position: absolute;
    top: 50%;
    width: 100px;
    -webkit-animation: pulsate 1s ease-out;
            animation: pulsate 1s ease-out;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}

@-webkit-keyframes pulsate {
    0% {
        -webkit-transform: scale(.1);
                transform: scale(.1);
        opacity: 0.0;
    }
    50% {
        opacity: 1;
    }
    100% {
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
        opacity: 0;
    }
}

@keyframes pulsate {
    0% {
        -webkit-transform: scale(.1);
                transform: scale(.1);
        opacity: 0.0;
    }
    50% {
        opacity: 1;
    }
    100% {
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
        opacity: 0;
    }
}


.header {
    display: flex;
    justify-content: space-between;
    font-size: calc(7px + 2vmin);
    padding-top: 20px;
    padding-bottom: 35px;
    background-color: black;
    padding-left:20px;
    color: white;
}
img {
    height: 40px;
}

@media all and (max-width: 800px) {
    .header {
        justify-content: center;
    }
    img {
        height: 30px;
    }
}

@media all and (max-width: 500px) {
    .header {
        flex-direction: column;
    }
    img {
        height: 20px;
    }
}
.footer {
    display: flex;
    justify-content: flex-end;
    font-size: calc(3px + 2vmin);
    padding: 10px;
    background-color: black;
    color: white;
}

@media all and (max-width: 800px) {
    .footer {
        justify-content: center;
    }
}

@media all and (max-width: 500px) {
    .footer {
        flex-direction: column;
    }
}
